 'use strict';
 import {Point} from "./point";
 
export interface PositionInfo {
    // The x and y coordinates of the element in the viewport.
    clientCoordinates: Point;

    // The width of the element.
    width: number;

    // The height of the element.
    height: number;

    // The viewport width.
    viewPortWidth: number;

    // The viewport height.
    viewPortHeight: number;

    // The horizontal scroll offset.
    scrollOffsetLeft: number;

    // The vertical scroll offset.
    scrollOffsetTop: number;

    // The offset from the top of the parent element
    elementOffsetTop: number;

    // The width offset used if the element is to be positioned on the other side of a parent element.
    widthOffset: number;
}